\t{{ t('richdocuments', 'Add new token') }}\n\t\t</button>\n\t\t<input type=\"button\" value=\"Save\" :disabled=\"disabled\"\n\t\t\t@click=\"updateTokens\">\n\t</div>\n</template>\n\n<script>\nconst generateRandomToken = () => {\n\tconst len = 3\n\tconst array = new Uint32Array(len)\n\twindow.crypto.getRandomValues(array)\n\tlet random = ''\n\tfor (var i = 0; i < len; ++i) {\n\t\trandom += array[i].toString(36)\n\t}\n\treturn random\n}\n\nconst appsStringToArray = (apps) => {\n\tif (apps === '') {\n\t\treturn []\n\t}\n\treturn apps.replace(/,$/g, '').split(',').map((item) => {\n\t\tconst data = item.split(':')\n\t\treturn {\n\t\t\tdescription: data[0],\n\t\t\ttoken: data[1]\n\t\t}\n\t})\n}\nconst appsArrayToString = (apps) => {\n\tlet result = ''\n\tapps.forEach((item) => {\n\t\tresult += item.description + ':' + item.token + ','\n\t})\n\treturn result\n}\nexport default {\n\tname: 'SettingsExternalApps',\n\tprops: {\n\t\texternalApps: {\n\t\t\ttype: String,\n\t\t\tdefault: ''\n\t\t},\n\t\tdisabled: {\n\t\t\ttype: Boolean,\n\t\t\tdefault: false\n\t\t}\n\t},\n\tdata() {\n\t\treturn {\n\t\t\ttokens: []\n\t\t}\n\t},\n\tbeforeMount() {\n\t\tthis.tokens = appsStringToArray(this.externalApps)\n\t},\n\tmethods: {\n\t\taddNewToken() {\n\t\t\tthis.tokens.push({\n\t\t\t\tdescription: '',\n\t\t\t\ttoken: generateRandomToken()\n\t\t\t})\n\t\t},\n\t\tremoveToken(token) {\n\t\t\tthis.tokens.splice(this.tokens.indexOf(token), 1)\n\t\t},\n\t\tupdateTokens() {\n\t\t\tthis.$emit('input', appsArrayToString(this.tokens))\n\t\t},\n\t\tregenerateToken(token) {\n\t\t\ttoken.token = generateRandomToken()\n\t\t}\n\t}\n}\n</script>\n\n<style scoped>\n\tli {\n\t\tdisplay: flex;\n\t}\n\t.token {\n\t\twidth: 200px;\n\t\tbackground-color: #fff;\n\t\tbackground-color: var(--color-main-background) !important;\n\t\tcolor: var(--color-text-light);\n\t\ttext-align: center;\n\t}\n</style>\n"],"sourceRoot":""}]);
